          clip-path: polygon(0 0, 100% 0, 100% 69%, 0 100%);
  grid-row: header;
  z-index: 100;
  width: 100vw;
}

.main {
  grid-row: main;
  height: auto;
}

.footer {
  background-color: var(--grey-shade);
  grid-row: footer;
  bottom: 0;
  width: 100vw;
  height: 80px;
