  margin-top: 2em;
  justify-self: center;
  box-shadow: var(--soft-shadow);
  border-radius: 15px;
}
.hidden {
  display: none;
}

.image-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: 1 /1 /2/2;
  height: 100%;
}
.create-profile-image {
  border: var(--border-blue) 3px solid;
  height: 9em;
  width: 9rem;
  object-fit: cover;
  border-radius: 50%;
  justify-self: center;
  align-items: center;
}

.create-profile-title {
  color: var(--white);
  font-family: var(--cookie-text-title);
  font-size: 3em;
  transform: rotate(-20deg) skew(-20deg);
}

.name-and-bio-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-area: 2 /1 /3/2;
  height: 100%;
}

.wrapper-name-title {
  width: 80%;
  text-align: left;
  font-family: var(--normal-text-title);
  color: var(--white);
  font-size: 1em;
}

.wrapper-bio-title {
  width: 80%;
  text-align: left;
  font-family: var(--normal-text-title);
  color: var(--white);
  font-size: 1em;
}

.wrapper-name {
  width: 80%;
  height: 20%;
  align-self: center;
  text-align: left;
  resize: none;
  box-shadow: var(--hard-shadow);
  border-radius: 5px;
  margin: 5px 0;
}
.wrapper-bio {
  width: 80%;
  height: 40%;
  text-align: left;
  resize: none;
  box-shadow: var(--hard-shadow);
  border-radius: 5px;
  margin: 5px 0;
}

.input-file-label {
  display: flex;
  width: 100%;
  justify-content: center;
}
.browse-button {
  font-size: 1rem;
  margin: 0 5px;
}

.upload-button {
  margin: 0 1em;
  width: 30%;
  text-align: center;
  box-shadow: var(--hard-shadow);
}

.bio-counter {
  text-align: right;
  width: 80%;
  font-family: var(--cookie-text-title);
  opacity: 0.8;
  color: var(--white);
}

.bottom-wrapper {
  grid-area: 4 / 1 / 5 / 2;
  display: flex;
  width: 100%;
  justify-content: center;
}

.create-profile-save-button {
  font-size: 2.5rem;
  text-decoration: none;
  color: var(--deep-blue);
  border-radius: 50%;
  margin: 2vh;
  height: 60px;
  width: 60px;
  border: none;
  box-shadow: var(--hard-shadow);
}

